import React from 'react'

const Style = {
    zIndex: -1,
    position: 'fixed',
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: '0% 50%'
}

const Background = ({ img }) => (
    <div style={{ background: `url(${img.default})`, ...Style }}></div>
)

export default Background