import './App.css';
import React, { useState, useEffect } from 'react'
import JsonData from './config/catalog.json'
import { BrowserRouter, Route } from 'react-router-dom'
import Background from './components/Background'
import Tab from './components/Tab'
import Search from './components/Search'
import BackTop from './components/BackTop'
import Home from './components/Home'
import Catalog from './components/Catalog'
import Article from './components/Article'

// 背景图片
const BackgroundImg = require('./config/background.jpg')

function App() {
  // DataToArr，把jsonData转化为Arr形式
  useEffect(() => { // 把数据整理成Array形式
    let arr = []
    Reflect.ownKeys(JsonData).forEach(tab => {
        Reflect.ownKeys(JsonData[tab]).forEach(label => {
            const result = JsonData[tab][label].map(item => ({
                id: item.id,
                title: item.title,
                extra: `${tab} —— ${label}`,
                time: item.time
            }))
            arr = [ ...arr, ...result ]
        })
    })
    sessionStorage.setItem('jsonDataArr', JSON.stringify(arr))
  }, [])

  const [ isPC, setIsPC ] = useState(true)
  useEffect(() => {
    const clientWidth = document.documentElement.clientWidth;
    setIsPC( clientWidth <= 750 ? false : true )
  }, [])

  // 所有标签
  const tabs = Reflect.ownKeys(JsonData)

  return (
    <div className="App">
      <Background img={BackgroundImg} />
      <BrowserRouter>
        <>
          <Tab tabs={tabs} isPC={isPC} />
          <Search />
          <BackTop isPC={isPC} />
          <Route path="/" exact component={Home}></Route>
          <Route path="/catalog/:tab" exact component={Catalog}></Route>  
          <Route path="/article/:id" exact component={Article}></Route>  
        </>
      </BrowserRouter>
    </div>
  );
}

export default App;
