import React from 'react'
import { Link } from 'react-router-dom'
import miment from 'miment'
import './Home.scss'

const jsonDataArr = (JSON.parse( sessionStorage.getItem('jsonDataArr') || [] )).sort((a, b) => b.time - a.time)

const Home = () => (
    <ul>
        { jsonDataArr.map(item => (
            <li key={item.id} className="item-container">
                <h3><Link to={`/article/${item.id}`}>{ item.title }</Link></h3>
                <div className="extra">{ `分类：${item.extra}` }</div>
                <div className="time">{ `创建时间：${miment(item.time).format()}` }</div>
            </li>
        )) }
    </ul>
)


export default Home